<template>
  <custom-dialog v-model="visible" :full="true" @close="onClose">
    <div class="comp deposit_qrcode">
      <div class="deposit_qrcode_title">
        <div class="title_text text_1">{{ $t('UI_Deposit_Informacoes') }}</div>
        <icon-font class="title_close" name="icon-Icon_Cloose" :size="18" @click="onClose" />
      </div>
      <div class="deposit_qrcode_content">
        <div class="pix_box">
          <img class="pix_img" :src="loadIcon('recharge_plx')" alt="" />
          <span class="amount">{{ symbolText }}{{ thousandFun(payAmount) }}</span>
        </div>
        <div class="deposit_qrcode_box">
          <div class="deposit_qrcode_box_tip">{{ $t('UI_Please_open') }}</div>
          <div class="deposit_qrcode_img">
            <img :src="payQrCode" alt="" />
          </div>
          <div class="deposit_qrcode_copy_btn">
            <div v-zoom class="btn btn_radius t_primary_dark_color" @click="onCopy">
              {{ $t('UI_Deposit_copy') }}
            </div>
          </div>
          <div class="deposit_qrcode_desc" v-html="pixDesc"></div>
        </div>
      </div>
    </div>
  </custom-dialog>
</template>

<script>
import { mapState } from 'vuex'
import { isFormatMillageAndNumber } from '@/utils/helper.js'
import copy from 'copy-to-clipboard'
import loadIcon from '@/assets/icons.js'

export default {
  data() {
    return {
      // 弹窗显隐藏
      visible: false,
      // 充值数据
      payInfo: {},
      // 是否 usdt
      isUsdt: false
    }
  },
  computed: {
    ...mapState('base', ['systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    // 金额标识
    currencySymbol() {
      const { currency_symbol = '' } = this.systemConfig
      return this.currencyShow ? currency_symbol : ''
    },
    // 金额标识显示
    symbolText() {
      return this.isUsdt ? 'USDT' : this.currencySymbol
    },
    // 充值金额
    payAmount() {
      const { amount = 0 } = this.payInfo

      return amount
    },
    // 充值二维码
    payQrCode() {
      const { img = '' } = this.payInfo

      return img
    },
    // pix说明
    pixDesc() {
      const { scan_to_pay_under_text = '' } = this.systemConfig
      return scan_to_pay_under_text
    }
  },
  methods: {
    loadIcon: str => loadIcon(str, false),
    /**
     * 千分比转换
     * @param val 参数
     * @param status 是否转为千分比 true转 false不转
     */
    thousandFun(val = '', status = true) {
      // 是否转为千分比开关 thousand_mark 1开 0关
      const { thousand_mark = 0 } = this.systemConfig
      return isFormatMillageAndNumber(val, status, thousand_mark)
    },
    /**
     * 打开弹窗
     * @param isUsdt 是否 usdt
     * @param payInfo 充值数据
     */
    open({ isUsdt = false, payInfo = {} } = {}) {
      this.isUsdt = isUsdt
      this.payInfo = payInfo
      this.visible = true
    },
    // 关闭
    onClose() {
      this.visible = false
    },
    // 拷贝编号
    onCopy() {
      const { card_num = '', order_id = '' } = this.payInfo
      const isCopy = copy(this.isUsdt ? order_id : card_num)
      if (isCopy) this.$pop.message().show({ content: this.$t('UI_Copied') })
    }
  }
}
</script>

