<template>
  <div class="comp activity_item">
    <!-- 活动名称 -->
    <div class="lt text_1">{{ info.name || '' }}</div>
    <div class="rt">
      <!-- 活动时间 -->
      <template v-if="info.countTime">
        <div class="activity_item_time">{{ formatTime(info.countTime) }}</div>
      </template>
      <!-- 活动进度 -->
      <template v-if="info.tip_count_switch == 1">
        <div class="activity_item_progr">{{ `${info.use_num}` }}/{{ `${info.total_num}` }}</div>
      </template>
    </div>
  </div>
</template>

<script>
import { datetime } from '@/utils/helper.js'

export default {
  props: {
    // 活动数据
    activityItem: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      // 活动数据
      info: {}
    }
  },
  created() {
    this.handleInfo()
  },
  beforeDestroy() {
    if (this.timer) {
      clearTimeout(this.timer)
      this.timer = null
    }
  },
  methods: {
    /**
     * 活动数据处理
     * cur_time: 服务器时间
     * end_time_stamp: 活动结束时间
     */
    handleInfo() {
      let countTime = 0
      const { diffSec = 0, end_time_stamp = 0 } = this.activityItem

      if (end_time_stamp && Number(end_time_stamp)) {
        const localTime = Math.round(Date.now() / 1000) + diffSec
        countTime = Number(end_time_stamp) - localTime
      }

      // 开始倒计时
      this.info = { ...this.activityItem, countTime }
      if (countTime > 0) this.countDown()
    },
    // 倒计时
    countDown() {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }

      this.timer = setTimeout(() => {
        const newNum = this.info.countTime - 1
        this.info.countTime = newNum
        if (newNum <= 0) {
          clearTimeout(this.timer)
          this.timer = null
          this.$emit('endTime')
          return
        }
        this.countDown()
      }, 1 * 1000)
    },
    // 格式化时间
    formatTime(time = 0) {
      const { days = 0, hours = 0, minutes = 0, seconds = 0 } = datetime.getFormatTimeThree(time)
      if (days > 0) {
        const daysText = days > 9 ? `${days}${this.$t('UI_VIP_DATE_Days')}` : `${days}${this.$t('UI_VIP_DATE_Day')}`
        return `${daysText} ${hours}:${minutes}:${seconds}`
      }
      return `${hours}:${minutes}:${seconds}`
    }
  }
}
</script>

