<template>
  <div class="comp channel_type">
    <template v-if="isShowInfo">
      <div class="channel_type_title">
        <div class="lt">{{ $t('UI_Select_Payment') }}</div>
        <div class="rt" @click="isShow = !isShow">
          <icon-font :name="isShow ? 'icon-Icon_Up' : 'icon-Icon_Down'" size="18" dark="t_text_color_4" />
        </div>
      </div>
    </template>
    <template v-if="newList.length">
      <div v-show="isShow" class="channel_type_list">
        <div
          v-for="item in newList"
          :key="item.type_id"
          class="channel_type_item"
          :class="{ active: value == item.type_id }"
          @click="$emit('change', item.type_id)"
        >
          <div class="channel_type_item_img">
            <img :src="item.logo" alt="" />
          </div>
          <div class="channel_type_item_text text_1">{{ item.type_name || '' }}</div>
          <!-- icon -->
          <template v-if="value == item.type_id">
            <div class="channel_type_item_icon">
              <icon-font name="icon-check_rightdown" size="18" />
            </div>
          </template>
        </div>
      </div>
    </template>
    <template v-if="Object.keys(currentTypeItem).length && isShowInfo">
      <div class="channel_type_info">
        <div class="channel_type_name">{{ currentTypeItem.type_name || '' }}</div>
        <div class="channel_type_limit">
          {{ ` ${$t('UI_Minimum_limit')}: ${currentTypeItem.amount_min || 0}~${currentTypeItem.amount_max || 0}` }}
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { getEnvConfig } from '@/utils/collect.js'

export default {
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    // 通道类型id
    value: {
      type: [String, Number],
      default: ''
    },
    // 通道类型列表
    list: {
      type: Array,
      default: () => []
    },
    // 是否显示其余信息
    isShowInfo: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      isShow: true
    }
  },
  computed: {
    // 当前通道列表
    newList() {
      return this.list.map(item => {
        const { logo = '', logo_url = '' } = item
        let newLogo = logo || logo_url || ''
        newLogo = newLogo ? (newLogo.includes('http') ? newLogo : `${getEnvConfig.baseURL()}${newLogo}`) : ''

        return {
          ...item,
          logo: newLogo
        }
      })
    },
    // 当前通道类型数据
    currentTypeItem() {
      const info = this.newList.find(item => item.type_id == this.value) || {}
      return info
    }
  }
}
</script>

