  width: 100%;
  height: 100%;
  position: relative;
  @include setBox(column, center, center);
  .deposit_detail_info {
    flex-grow: 1;
    flex-shrink: 1;
    width: 100%;
    padding: 32px 16px 48px 16px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      display: block !important;
      width: 4px;
    }
  }
  .deposit_detail_btn {
    flex-grow: 0;
    flex-shrink: 0;
    width: 100%;
    padding: 14px 16px;
    background: $page_dialog_bg_2;
    border-top: 1px solid $border_hr_color;
    .btn {
      width: 100%;
      height: 44px;
      font-size: 14px;
      font-weight: 600;
      line-height: 1;
      cursor: pointer;
      @include setBox(row, center, center);
    }
  }
  .deposit_kefu_inlet {
    position: absolute;
    bottom: 120px;
    right: 10px;
  }
}

