<template>
  <custom-dialog v-model="visible" :full="true" @close="onClose">
    <div class="comp deposit_spel">
      <!-- 头部 -->
      <div class="deposit_spel_title">
        <div class="title_text text_1">{{ $t('UI_Remittance_Period') }}</div>
        <icon-font class="title_close" name="icon-Icon_Cloose" :size="18" @click="onClose" />
      </div>
      <!-- 内容 -->
      <div class="deposit_spel_content">
        <!-- 标题 -->
        <div class="content_title">{{ $t('UI_Recharge') }}</div>
        <!-- 倒计时 -->
        <div class="content_down">
          <div class="content_down_text">{{ $t('UI_Payment_Countdown') }}</div>
          <div class="content_down_time">{{ countTimeText }}</div>
        </div>
        <!-- 支付信息 -->
        <div class="content_pay_info">
          <div class="content_pay_info_title">{{ $t('UI_Payment_information') }}</div>
          <div class="content_pay_info_list">
            <div v-for="(item, i) in payList" :key="i" class="content_pay_info_item">
              <div class="pay_item_label">{{ $t(item.label) }}</div>
              <div class="pay_item_info">
                <div class="text text_1">{{ item.value }}</div>
                <div v-zoom class="btn btn_radius t_primary_dark_color" @click="onCopy(item.value)">
                  {{ $t('UI_copy') }}
                </div>
              </div>
            </div>
          </div>
          <div class="pay_amount">{{ $t('UI_Order_amount') }}: {{ symbolText }}{{ thousandFun(payAmount) }}</div>
        </div>
        <!-- 说明 -->
        <div class="content_pay_tip">
          <div class="tip_title">{{ $t(spelTip.title) }}</div>
          <div class="tip_list">
            <div v-for="text in spelTip.list" :key="text" class="tip_item_text">{{ $t(text) }}</div>
          </div>
        </div>
      </div>
    </div>
  </custom-dialog>
</template>

<script>
import { mapState } from 'vuex'
import { isFormatMillageAndNumber, formattedTime } from '@/utils/helper.js'
import copy from 'copy-to-clipboard'

export default {
  data() {
    return {
      // 弹窗显隐藏
      visible: false,
      // 充值数据
      payInfo: {},
      // 是否 usdt
      isUsdt: false,
      // 倒计时
      countTime: 0
    }
  },
  computed: {
    ...mapState('base', ['systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    // 金额标识
    currencySymbol() {
      const { currency_symbol = '' } = this.systemConfig
      return this.currencyShow ? currency_symbol : ''
    },
    // 金额标识显示
    symbolText() {
      return this.isUsdt ? 'USDT' : this.currencySymbol
    },
    // 支付倒计时显示
    countTimeText() {
      return formattedTime(this.countTime)
    },
    // 支付信息
    payList() {
      return [
        { label: 'UI_Provide', value: this.payInfo.pay_type || '' },
        { label: 'UI_Account_number', value: this.payInfo.account || '' },
        { label: 'UI_Beneficiary_name', value: this.payInfo.name || '' }
      ]
    },
    // 充值金额
    payAmount() {
      const { amount = 0 } = this.payInfo

      return amount
    },
    // 说明
    spelTip() {
      return {
        title: 'UI_Process',
        list: ['UI_Process_text1', 'UI_Process_text2', 'UI_Process_text3']
      }
    }
  },
  methods: {
    /**
     * 千分比转换
     * @param val 参数
     * @param status 是否转为千分比 true转 false不转
     */
    thousandFun(val = '', status = true) {
      // 是否转为千分比开关 thousand_mark 1开 0关
      const { thousand_mark = 0 } = this.systemConfig
      return isFormatMillageAndNumber(val, status, thousand_mark)
    },
    // 倒计时
    countDown() {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }

      this.timer = setTimeout(() => {
        if (this.countTime <= 0) {
          clearTimeout(this.timer)
          this.timer = null
          this.visible = false
        } else {
          this.countTime--
          this.countDown()
        }
      }, 1 * 1000)
    },
    /**
     * 打开弹窗
     * @param isUsdt 是否 usdt
     * @param payInfo 充值数据
     */
    open({ isUsdt = false, payInfo = {} } = {}) {
      this.isUsdt = isUsdt
      this.payInfo = payInfo
      this.visible = true

      // 支付倒计时开始
      const { expire_time = 0, time = 0 } = this.payInfo
      this.countTime = expire_time - time
      this.countDown()
    },
    // 关闭
    onClose() {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }

      this.visible = false
    },
    // 拷贝
    onCopy(str = '') {
      if (!String(str).trim()) return

      const isCopy = copy(str)
      if (isCopy) this.$pop.message().show({ content: this.$t('UI_Copied') })
    }
  }
}
</script>

