<template>
  <custom-dialog v-model="visible" @close="onClose">
    <div class="comp deposit_info">
      <div class="deposit_info_title">
        <div class="deposit_info_text text_1">{{ $t('UI.Charge_1_info_1') }}</div>
        <div class="deposit_info_close">
          <icon-font name="icon-Icon_Cloose" dark="t_text_color_2" size="18" @click="onClose" />
        </div>
      </div>
      <div class="deposit_info_content">
        <div class="content_gold">
          <div class="content_gold_top">
            <!-- 当前充值金额 -->
            <div class="gold_item">
              <div class="gold_item_t" :class="{ text_through: isActivity }">{{ thousandFun(payAmount) }}</div>
              <div v-if="isUsdt" class="gold_item_b">USDT</div>
            </div>
            <template v-if="giftAmountInfo">
              <!-- 当前充值金额 + 赠送比例 -->
              <template v-if="giftAmountInfo.amount || giftAmountInfo.percent">
                <span class="gold_item_add">+</span>
                <div class="gold_item">
                  <div class="gold_item_t">{{ thousandFun(giftAmountInfo.amount) }}</div>
                  <div class="gold_item_b">{{ giftAmountInfo.percent }}%</div>
                </div>
              </template>
              <!-- 赠送金额 -->
              <template v-if="giftAmountInfo.gold_all">
                <span class="gold_item_add">+</span>
                <div class="gold_item">
                  <div class="gold_item_t deliver">{{ thousandFun(giftAmountInfo.gold_all) }}</div>
                </div>
              </template>
            </template>
          </div>
          <div class="content_gold_bottom">
            <div class="gold_bottom_lt">
              <template v-if="isUsdt">{{ `1 USDT ≈ ${usdtRate}` }}</template>
            </div>
            <div class="gold_bottom_rt">
              <template v-if="!isUsdt">
                <img v-if="!currencySymbol" class="gold_bottom_rt_img" :src="loadIcon('deposit_gold_icon')" alt="" />
                <span class="gold_bottom_rt_text">{{ currencySymbol }}</span>
              </template>
              <span class="gold_bottom_rt_text">{{ totalAmount }}</span>
            </div>
          </div>
        </div>
        <deposit-fields ref="depositFieldRef" :list="fieldList" />
      </div>
      <div class="deposit_info_btn">
        <div v-zoom class="btn btn_radius t_primary_dark_color" @click="onSubmit">
          {{ $t('General.General_1') }}
        </div>
      </div>
    </div>
  </custom-dialog>
</template>

<script>
import { mapState } from 'vuex'
import { calcFun } from '../deposit.js'
import { isFormatMillageAndNumber, getToFixed } from '@/utils/helper.js'
import { bankDepositTemp } from '../depositConfig.js'
import loadIcon from '@/assets/icons.js'
import DepositFields from './deposit-fields.vue'

export default {
  components: {
    DepositFields
  },
  data() {
    return {
      // 弹窗显隐藏
      visible: false,
      // 充值金额
      payAmount: '',
      // 是否 usdt
      isUsdt: false,
      // 是否参与活动
      isActivity: false,
      // 金额/活动信息
      payActivityInfo: {},
      // 渠道信息
      channelInfo: {},
      // usdt总计金额显示
      payUsdtTotalAmount: ''
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'systemConfig', 'languageType']),
    ...mapState('switchState', ['currencyShow']),
    // 金额标识
    currencySymbol() {
      const { currency_symbol = '' } = this.systemConfig
      return this.currencyShow ? currency_symbol : ''
    },
    // 赠送金额信息
    giftAmountInfo() {
      if (!this.isActivity) return null

      const { giftInfo = {}, up_limit_send_amount = 0, gift_amount = 0, fixed_gold = 0 } = this.payActivityInfo
      const { no_limit_activity_amount = 0, activity_percent = 0, channel_amount = 0, channel_percent = 0 } = giftInfo
      const upLimitAmount = parseFloat(up_limit_send_amount || 0)

      if (upLimitAmount > 0 && no_limit_activity_amount > upLimitAmount) return { gold_all: gift_amount }

      return {
        amount: getToFixed(calcFun(no_limit_activity_amount, channel_amount, '+')),
        percent: calcFun(calcFun(activity_percent, channel_percent, '+'), 100, '*'),
        gold_all: fixed_gold
      }
    },
    // usdt汇率
    usdtRate() {
      const { rate = '' } = this.channelInfo
      return `${this.currencySymbol}${rate}`
    },
    // 充值总金额
    totalAmount() {
      let amount = 0

      if (this.isUsdt) {
        amount = this.payUsdtTotalAmount
      } else {
        const { gift_amount = 0 } = this.payActivityInfo
        amount = this.thousandFun(Number(this.payAmount || 0) + Number(gift_amount || 0))
      }

      return amount
    },
    /**
     * 提交表单列表模板构造
     * pay_fill_conf 是否填充信息
     */
    fieldList() {
      const { pay_fill_conf = {} } = this.channelInfo
      const list = bankDepositTemp({ payConfig: pay_fill_conf })

      return list
    }
  },
  methods: {
    loadIcon: str => loadIcon(str, false),
    /**
     * 千分比转换
     * @param val 参数
     * @param status 是否转为千分比 true转 false不转
     */
    thousandFun(val = '', status = true) {
      // 是否转为千分比开关 thousand_mark 1开 0关
      const { thousand_mark = 0 } = this.systemConfig
      return isFormatMillageAndNumber(val, status, thousand_mark)
    },
    /**
     * 打开弹窗
     * @param payAmount 充值金额
     * @param isUsdt 是否 usdt
     * @param isActivity 是否参与活动
     * @param payActivityInfo 金额/活动信息
     * @param payUsdtTotalAmount usdt总计金额显示
     * @param channelInfo 渠道信息
     */
    open({
      payAmount = '',
      isUsdt = false,
      isActivity = false,
      payActivityInfo = {},
      channelInfo = {},
      payUsdtTotalAmount = ''
    } = {}) {
      this.payAmount = payAmount
      this.isUsdt = isUsdt
      this.isActivity = isActivity
      this.payActivityInfo = payActivityInfo
      this.payUsdtTotalAmount = payUsdtTotalAmount
      this.channelInfo = channelInfo

      return new Promise((resolve, reject) => {
        this.resolve = resolve
        this.reject = reject
        this.visible = true
      })
    },
    // 关闭
    onClose() {
      this.visible = false
      this.reject('cancel')
    },
    // 充值提交
    onSubmit() {
      this.$nextTick(() => {
        const depositFieldRef = this.$refs['depositFieldRef']
        if (!depositFieldRef) return this.onClose()

        const params = depositFieldRef.getFormatDate()
        if (!params) return

        this.visible = false
        this.resolve(params)
      })
    }
  }
}
</script>

