<template>
  <div class="comp deposit_fields">
    <div v-for="item in fieldList" :key="item.key" class="field_line">
      <!-- 标题 -->
      <div class="field_line_title">
        <div class="text">{{ $t(item.label) }}</div>
        <div v-if="item.required" class="color_red">*</div>
      </div>
      <!-- 输入框 -->
      <template v-if="item.type == 'input'">
        <div class="field_line_box">
          <input
            v-model="item.value"
            class="field_input input_radius"
            type="text"
            :placeholder="item.placeholder ? $t(item.placeholder) : ''"
            :disabled="item.disabled || false"
            @blur="onBlur(item)"
          />
          <div class="field_line_text" v-html="item.msgText"></div>
        </div>
      </template>
      <!-- 单选框 -->
      <template v-if="item.type == 'radio'">
        <div class="field_line_box">
          <v-radio-group
            v-model="item.value"
            class="field_line_radio"
            :disabled="item.disabled || false"
            hide-details
            row
          >
            <v-radio
              v-for="ridioItem in item.radioList"
              :key="ridioItem.value"
              :value="ridioItem.value"
              :ripple="false"
              color="t_primary_dark_color"
            >
              <template v-slot:label>
                <div class="field_line_radio_title">{{ $t(ridioItem.label) }}</div>
              </template>
            </v-radio>
          </v-radio-group>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { createCPF, againstCPF } from '@/utils/helper.js'

export default {
  props: {
    // 表单列表
    list: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      fieldList: []
    }
  },
  watch: {
    list: {
      handler() {
        this.fieldList = this.list
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    // 输入框失去焦点时
    onBlur(item = {}) {
      item.value = String(item.value).trim()
      // 是否需要格式化显示(cpf)
      if (item.format == 'cpf') item.value = createCPF(item.value)
    },
    // 表单重置
    reset() {
      this.fieldList.forEach(item => {
        item.value = ''
      })
    },
    /**
     * 前端校验
     * 增加 isCheck 字段，判断校验是否通过
     */
    checkFormatDate() {
      let newList = []
      // 递归方法，校验内层数据
      const recursionCheck = (list = []) => {
        return list.map(item => {
          // cpf 显示格式解除
          const value = item.format == 'cpf' ? againstCPF(item.value) : item.value
          const isCheck = item.rules(value)

          let newItem = { ...item, isCheck: item.required ? isCheck : true }
          newList.push(newItem)

          return newItem
        })
      }
      this.fieldList = recursionCheck(this.fieldList)

      // 校验错误
      const errorList = newList.filter(item => !item.isCheck).map(m => this.$t(m.rulesText))
      // 校验不通过
      if (errorList.length > 0) {
        this.$pop.message().show({ content: errorList[0] })
        return false
      }

      return newList
    },
    // 获取表单数据，验证校验
    getFormatDate() {
      // 前端校验
      const webCheckList = this.checkFormatDate()
      if (!webCheckList) return false
      // 前端校验通过返回参数
      let params = {}
      webCheckList.forEach(item => {
        let val = item.value || ''
        // cpf 显示格式解除
        if (item.format == 'cpf') val = againstCPF(item.value)
        params[item.key] = val
      })

      return params
    }
  }
}
</script>

