<template>
  <div class="deposit version_one">
    <template v-if="isEmpty">
      <empty :tip-text="$t('UI_No_Channel')" />
    </template>
    <template v-if="channelList.length">
      <!-- 通道 -->
      <channel-list v-model="channelId" :list="channelList" @change="onChannelSwitch" />
      <!-- 分割线 -->
      <div class="divider"></div>
      <!-- 金额title -->
      <div class="deposit_title_text">{{ $t('UI_CASHOUT_ENTERTOP') }}</div>
      <!-- 金额选项 -->
      <amount-list v-model="amountId" :is-usdt="isUsdt" :list="amountList" @change="onChannelAmount" />
      <!-- 金额输入框 -->
      <div class="deposit_input_box">
        <amount-input
          v-model="amountInput"
          :is-usdt="isUsdt"
          :is-custom="isCustom"
          :activity-info="activityInfo"
          @change="onChannelInput"
        />
      </div>
      <!-- usdt总计显示 -->
      <template v-if="isUsdt">
        <div class="usdt_rate_box">
          <span class="rate_text">{{ `1 USDT ≈ ${currentSymbol}${channelItem.rate}` }}</span>
          <span class="total_text">{{ `${$t('UI_Total')}:${usdtTotalAmount}` }}</span>
        </div>
      </template>
      <!-- 参与的活动 -->
      <template v-if="activityList.length">
        <div class="activity_box">
          <activity-item
            v-for="(item, i) in activityList"
            :key="new Date().getTime() + i"
            :activity-item="item"
            @endTime="onEndTime"
          />
        </div>
      </template>
      <!-- 活动开关 -->
      <div class="deposit_check_box">
        <check-box v-model="movableSwitch">
          <div class="check_text">{{ $t('UI_With_Bonus') }}</div>
        </check-box>
      </div>
      <!-- 分割线 -->
      <div class="divider"></div>
      <!-- 充值提示 -->
      <template v-if="channelItem.recharge_tips">
        <div class="deposit_tips" v-html="channelItem.recharge_tips"></div>
      </template>
      <!-- 充值记录按钮 -->
      <div class="deposit_record_box">
        <span class="deposit_record_text" @click="historyState = true">{{ $t('UI_Deposit_Withdraw_Notes') }}</span>
      </div>
      <!-- 充值注意事项 -->
      <div class="deposit_notes">
        <div class="deposit_notes_title">{{ $t('UI_Deposit_Notes') }}</div>
        <div class="deposit_notes_content">{{ noticeDesc }}</div>
      </div>
    </template>

    <!-- 充值记录 -->
    <custom-dialog v-model="historyState" :full="true">
      <recharge-history
        :tab="3"
        :style="{ width: isApp ? '100%' : '650', borderRadius: !isApp ? '8px' : '' }"
        @close="historyState = false"
      />
    </custom-dialog>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { goodsList } from '@/api/modules/config.js'
import { getToFixed, isFormatMillageAndNumber, throttle } from '@/utils/helper.js'
import { calcRechargeGiftAmountV1, calcUsdtTotalAmount } from '../deposit.js'
import ChannelList from './ui/channel-list.vue'
import AmountList from '../ui/amount-list.vue'
import AmountInput from '../ui/amount-input.vue'
import ActivityItem from '../ui/activity-item.vue'
import CheckBox from '@/components/ui/CheckBox'
import RechargeHistory from '@/components/ui/withdrawal-recharge-history'
import Empty from '@/components/ui/empty'

export default {
  components: {
    ChannelList,
    AmountList,
    AmountInput,
    ActivityItem,
    CheckBox,
    RechargeHistory,
    Empty
  },
  data() {
    return {
      // 是否显示缺省
      isEmpty: false,
      // 通道列表
      channelList: [],
      // 通道id
      channelId: '',
      // 当前选中金额id【amount_id】
      amountId: '',
      // 输入框金额
      amountInput: '',
      // 活动开关
      movableSwitch: true,
      // 充值记录
      historyState: false
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    ...mapGetters('base', ['getRootParams']),
    // 当前货币符号
    currentSymbol() {
      const { currency_symbol = '' } = this.systemConfig

      return this.currencyShow ? currency_symbol : ''
    },
    // 默认充值金额
    rechargeAmount() {
      const { recharge_default = 0 } = this.systemConfig

      return recharge_default
    },
    // 当前通道数据处理合并
    channelItem() {
      const channelItem = this.channelList.find(item => item.id == this.channelId) || {}
      const { channel_list = [], ...rest } = channelItem
      const list = Array.isArray(channel_list) ? channel_list : []
      const item = list.find(item => item.channel_id == this.channelId) || {}

      return { ...rest, ...item }
    },
    /**
     * 当前通道是否为usdt
     * 0:否 1:是
     */
    isUsdt() {
      const { is_usdt = 0 } = this.channelItem

      return is_usdt == 1
    },
    /**
     * 当前通道金额是否可自定义
     * 0:否 1:是
     */
    isCustom() {
      const { is_custom = 0 } = this.channelItem

      return is_custom == 1
    },
    // 当前通道金额列表
    amountList() {
      const { act_send = [] } = this.channelItem
      let list = Array.isArray(act_send) ? act_send : []
      list = list.map((item, i) => {
        // 前端活动开关开启时，计算赠送金额数据
        const giftInfo = this.movableSwitch ? calcRechargeGiftAmountV1(item, item.amount) : {}
        const { activity_amount = 0, channel_amount = 0 } = giftInfo
        return {
          ...item,
          amount_id: i + 1,
          giftInfo,
          gift_amount: getToFixed(activity_amount + channel_amount)
        }
      })

      return list
    },
    /**
     * 当前金额参与的金额/活动选项
     * 当前输入框的金额存在于当前金额列表中
     *  1.当前项全部赠送金额 = 渠道赠送金额 + 活动赠送金额
     * 当前输入框的金额不存在于当前金额列表中
     *  1.筛选出 <= 输入框金额项 && 金额档位取最大项
     *  2.当前项赠送金额 = 活动赠送金额 + 渠道赠送金额
     */
    activityInfo() {
      let info = {}

      const item = this.amountList.find(item => item.amount_id == this.amountId)
      // 当前输入框的金额存在于当前金额列表中
      if (item) {
        info = item
      } else {
        // 当前输入框的金额不存在于当前金额列表中
        const amountVal = parseFloat(this.amountInput || 0)
        // 取金额列表中 <= 当前输入框的金额项 && 取最大项
        const newAmountList = this.amountList
          .filter(item => Number(item.amount) <= amountVal)
          .sort((a, b) => b.amount - a.amount)

        info = newAmountList.length ? newAmountList[0] : {}
        // 前端活动开关开启时，计算赠送金额数据
        const giftInfo = this.movableSwitch ? calcRechargeGiftAmountV1(info, amountVal) : {}
        const { activity_amount = 0, channel_amount = 0 } = giftInfo
        info = {
          ...info,
          giftInfo,
          gift_amount: getToFixed(activity_amount + channel_amount)
        }
      }

      return info
    },
    /**
     * usdt总计金额显示
     * rate: USDT汇率
     * gift_amount: 活动赠送金额 + 渠道赠送金额
     * amount: 当前输入框的金额
     */
    usdtTotalAmount() {
      const { rate = 0 } = this.channelItem
      const { gift_amount = 0 } = this.activityInfo
      let totalAmount = calcUsdtTotalAmount(this.amountInput, gift_amount, rate)
      totalAmount = getToFixed(totalAmount)

      return this.thousandFun(totalAmount)
    },
    /**
     * 参与的活动列表
     * diffSec: 服务器时间和本地时间的差值
     * 普通活动 + 渠道活动
     */
    activityList() {
      if (!this.movableSwitch) return []

      const { diffSec = 0 } = this.channelItem
      const { activity_info = {}, channel_recharge_activity_info = [] } = this.activityInfo
      let list = Array.isArray(channel_recharge_activity_info) ? channel_recharge_activity_info : []
      list = [activity_info, ...list].filter(f => Object.keys(f).length)
      list = list.map(item => ({ ...item, diffSec }))

      return list
    },
    // 充值注意事项
    noticeDesc() {
      return this.channelItem.show_desc || ''
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // 获取通道数据
    async getGoodsList() {
      const params = {
        ...this.getRootParams,
        token: this.user?.token,
        uid: new Date().getTime(),
        Type: 100
      }
      const res = await goodsList(params).catch(e => e)
      if (res.code != 0) return

      // 设置通道列表
      const list = res.data && Array.isArray(res.data) ? res.data : []
      this.channelList = list.map(item => {
        // 计算服务器时间和本地时间的差值
        const { channel_list = [] } = item
        const newList = channel_list.filter(f => f.cur_time)
        const { cur_time = 0 } = newList[0] || {}
        const diffSec = Number(cur_time) - Math.round(Date.now() / 1000)

        return {
          ...item,
          diffSec
        }
      })
      // 设置默认通道id
      this.channelId = this.channelList.length ? this.channelList[0].id : ''
      // 设置是否显示缺省
      this.isEmpty = !this.channelList.length
      // 设置当前默认选中金额id
      const { amount_id = '' } = this.amountList.find(item => item.amount == this.rechargeAmount) || {}
      this.amountId = amount_id
      // 设置默认金额
      this.amountInput = String(this.rechargeAmount)
    },
    /**
     * 千分比转换
     * @param val 参数
     * @param status 是否转为千分比 true转为千分比 false转为数字
     * @return {string}
     */
    thousandFun(val = '', status = true) {
      // 是否转为千分比开关 thousand_mark 1开 0关
      const { thousand_mark = 0 } = this.systemConfig
      return isFormatMillageAndNumber(val, status, thousand_mark)
    },
    // 切换通道
    onChannelSwitch() {
      // 设置当前选中金额id
      const { amount_id = '' } = this.amountList.find(item => item.amount == this.rechargeAmount) || {}
      this.amountId = amount_id
      // 切换到默认金额
      this.onChannelAmount(amount_id)
    },
    // 切换金额
    onChannelAmount(amount_id = 0) {
      const item = this.amountList.find(item => item.amount_id == amount_id) || {}
      this.amountInput = String(item.amount || this.rechargeAmount)
    },
    // 输入框输入
    onChannelInput(val = '') {
      // 设置当前选中金额id
      const item = this.amountList.find(item => item.amount == val) || {}
      this.amountId = item.amount_id || ''
    },
    /**
     * 活动结束,更新数据
     * 使用节流，5秒内只执行一次
     */
    onEndTime() {
      throttle(this.getGoodsList, 1 * 5000)
    },
    // 获取充值数据
    getPayData() {
      const { amountInput, movableSwitch, channelItem, isUsdt, activityInfo, usdtTotalAmount, activityList } = this

      return {
        amountInput,
        movableSwitch,
        channelItem,
        isUsdt,
        activityInfo,
        usdtTotalAmount,
        activityList
      }
    }
  }
}
</script>

