<template>
  <div class="comp channel_list">
    <div class="channel_list_title">
      <div class="lt">{{ $t('UI_Select_channel') }}</div>
      <div class="rt" @click="isShow = !isShow">
        <icon-font :name="isShow ? 'icon-Icon_Up' : 'icon-Icon_Down'" size="18" dark="t_text_color_4" />
      </div>
    </div>
    <template v-if="list.length">
      <div v-show="isShow" class="channel_info_list">
        <div v-for="item in list" :key="item.channel_id" class="channel_item" @click="$emit('change', item.channel_id)">
          <div class="lt">
            <div class="channel_item_text text_1">{{ item.channel_name || '' }}</div>
            <div class="channel_item_limit text_1">
              {{ `${$t('UI_Minimum_limit')}: ${item.min_amount}~${item.max_amount}` }}
            </div>
          </div>
          <div class="rt">
            <radio-box v-model="item.check" />
          </div>
        </div>
      </div>
    </template>
    <template v-if="Object.keys(currentChannelInfo).length">
      <div class="channel_info">
        <div class="channel_info_text text_1">{{ currentChannelInfo.channel_name || '' }}</div>
        <div class="channel_info_limit text_1">
          {{ `${$t('UI_Minimum_limit')}: ${currentChannelInfo.min_amount}~${currentChannelInfo.max_amount}` }}
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import RadioBox from '@/components/ui/radioBox'

export default {
  components: {
    RadioBox
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    // 通道id
    value: {
      type: [String, Number],
      default: ''
    },
    // 通道列表
    list: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      isShow: true
    }
  },
  computed: {
    // 当前通道信息
    currentChannelInfo() {
      const info = this.list.find(item => item.channel_id == this.value) || {}

      return info
    }
  }
}
</script>

