/**
 * 模板
 * key 对应的字段
 * weight 权重对应的字段
 * type 模板类型
 * label 模板名称
 * placeholder 模板名称占位
 * value 模板值
 * required 是否必填
 * isShow 是否显示
 * format: 显示格式化 cpf格式
 * rules 校验方法
 * rulesText 校验失败信息
 * disabled 是否禁用 默认不禁用false
 * radioList radio选项列表
 * radioTempData radio选项对应的模板列表
 * selectList select选项列表
 * checkbox checkbox选项列表
 * msgText 输入框底部显示文本
 */

/**
 * radioList -> radio选项
 * label 名称
 * value 值
 * isShow 是否显示
 * weight 权重对应的字段
 */

/**
 * selectList -> select选项
 * label 名称
 * value 值
 */

import store from '@/store'

// 类型模板
const TEMP_TYPE = {
  INPUT: 'input',
  RADIO: 'radio',
  CHECKBOX: 'checkbox',
  SELECT: 'select'
}

// 校验
const rules = {
  // 输入框不能为空
  notEmpty: (val = '') => {
    const value = val.trim()
    return !!value
  },
  // cpf校验
  cpfCheck: (val = '') => {
    const value = val.trim()
    return /^\d{11}(?:\d{3})?$/.test(value)
  },
  // 巴基斯坦手机号校验
  pakPhoneCheck: (val = '') => {
    const value = val.trim()
    return /^\d{11}$/.test(value)
  }
}

/**
 * 巴西充值模板
 * auto_fill_status 自动填充 0关 1开 --> 【只针对姓名，邮箱，手机号】
 * auto_fill_unchange_status 自动填充不可变更 0关 1开 --> 【只针对姓名，邮箱，手机号】
 * cpf_tips 巴西-税号 说明文本
 * recharge_page_mobile_desc 手机号说明文本
 */
export const brazilTemp = (payConfig = {}) => {
  const { user = {}, systemConfig = {} } = store?.state?.base || {}
  const { pay_userinfo_fields = {}, recharge_page_mobile_desc = '' } = systemConfig
  const { pay = {}, cpf_tips = '' } = pay_userinfo_fields || {}
  const { recharge_fields = {} } = user
  const { auto_fill_status = 0, auto_fill_unchange_status = 0 } = payConfig

  const temp = [
    // 姓名
    {
      key: 'name',
      weight: 'name',
      type: TEMP_TYPE.INPUT,
      label: 'UI_Deposit_UserName',
      placeholder: 'UI.Charge_1_info_3',
      value: auto_fill_status == 1 ? recharge_fields.name || '' : '',
      required: true,
      disabled: auto_fill_unchange_status == 1 && auto_fill_status == 1 && recharge_fields.name ? true : false,
      isShow: pay['name'] == 1,
      rules: rules.notEmpty,
      rulesText: 'UI.Charge_1_info_3'
    },
    // 手机号
    {
      key: 'phone',
      weight: 'phone',
      type: TEMP_TYPE.INPUT,
      label: 'UI_Deposit_PhoneNumber',
      placeholder: 'UI.Charge_1_info_4',
      value: auto_fill_status == 1 ? recharge_fields.phone || '' : '',
      required: true,
      disabled: auto_fill_unchange_status == 1 && auto_fill_status == 1 && recharge_fields.phone ? true : false,
      isShow: pay['phone'] == 1,
      rules: rules.notEmpty,
      rulesText: 'UI.Charge_1_info_4',
      msgText: recharge_page_mobile_desc.replaceAll('\n', '<br/>')
    },
    // 邮箱
    {
      key: 'email',
      weight: 'email',
      type: TEMP_TYPE.INPUT,
      label: 'UI.cashout_1_bank_12',
      placeholder: 'UI_Deposit_emailPlaceholder',
      value: auto_fill_status == 1 ? recharge_fields.email || '' : '',
      required: true,
      disabled: auto_fill_unchange_status == 1 && auto_fill_status == 1 && recharge_fields.email ? true : false,
      isShow: pay['email'] == 1,
      rules: rules.notEmpty,
      rulesText: 'UI_Deposit_emailPlaceholder'
    },
    // 巴西-税号
    {
      key: 'taxid',
      weight: 'taxid',
      type: TEMP_TYPE.INPUT,
      label: 'UI_DepositId',
      placeholder: 'UI_Shop_InputTaxId',
      value: auto_fill_status == 1 ? recharge_fields.taxid || '' : '',
      required: true,
      isShow: pay['taxid'] == 1,
      format: 'cpf',
      rules: rules.cpfCheck,
      rulesText: 'UI_Shop_InputTaxId',
      msgText: cpf_tips.replaceAll('\n', '<br/>')
    },
    // 巴西-邮编
    {
      key: 'zipcode',
      weight: 'zipcode',
      type: TEMP_TYPE.INPUT,
      label: 'UI_DepositIdCode',
      placeholder: 'UI_Shop_InputZipCode',
      value: auto_fill_status == 1 ? recharge_fields.zipcode || '' : '',
      required: true,
      isShow: pay['zipcode'] == 1,
      rules: rules.notEmpty,
      rulesText: 'UI_Shop_InputZipCode'
    }
  ]
  const list = temp.filter(item => item.isShow)

  return list
}

