/**
 * 充值模板
 * @param {*} payConfig 是否填充信息
 * @param {*} payConfig -> auto_fill_status 自动填充 0关 1开 --> 【只针对姓名，邮箱，手机号】
 * @param {*} payConfig -> auto_fill_unchange_status 自动填充不可变更 0关 1开 --> 【只针对姓名，邮箱，手机号】
 * @param {*} version_position 1:印度 2:尼日利亚 3:越南 4:泰国 5:巴西 6:墨西哥 7:菲律宾 8:印尼 9:阿根廷 10:加纳 11:哥伦比亚 12:喀麦隆 13:智利 14:巴基斯坦 15:澳大利亚 16:孟加拉国 17:肯尼亚 18:土耳其
 */
export const bankDepositTemp = ({ payConfig = {} } = {}) => {
  const { systemConfig = {} } = store?.state?.base || {}
  const { version_position = 1 } = systemConfig || {}

  // 巴西
  if (version_position == 5) return brazilTemp(payConfig)
  // 墨西哥
  if (version_position == 6) return mexicoTemp(payConfig)
  // 巴基斯坦
  if (version_position == 14) return pakistanTemp(payConfig)
  // 土耳其
  if (version_position == 18) return turkeyTemp(payConfig)

  return []
}
