<template>
  <div class="comp amount_list">
    <div
      v-for="item in list"
      :key="item.amount_id"
      class="amount_item"
      :class="{ active: amountId == item.amount_id }"
      @click="$emit('change', item.amount_id)"
    >
      <div class="amount_item_text">
        <span class="symbol">{{ symbolText }}</span>
        <span class="amount">{{ thousandFun(item.amount || 0) }}</span>
      </div>
      <!-- 赠送金额 -->
      <template v-if="item.gift_amount">
        <div class="gift_amount">{{ giftSymbolText }}{{ thousandFun(item.gift_amount) }}</div>
      </template>
      <!-- icon -->
      <template v-if="amountId == item.amount_id">
        <div class="amount_item_icon">
          <icon-font name="icon-check_rightdown" size="18" />
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { isFormatMillageAndNumber } from '@/utils/helper.js'

export default {
  model: {
    prop: 'amountId',
    event: 'change'
  },
  props: {
    // 选中的金额id
    amountId: {
      type: [Number, String],
      default: 0
    },
    // 是否为 usdt
    isUsdt: {
      type: Boolean,
      default: false
    },
    // 金额列表
    list: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    // 金额标识符显示
    symbolText() {
      // 当前货币符号
      const { currency_symbol = '' } = this.systemConfig
      const symbol = this.currencyShow ? currency_symbol : ''

      return this.isUsdt ? 'USDT' : symbol
    },
    // 赠送金额标识符显示
    giftSymbolText() {
      return !this.isUsdt ? `+${this.symbolText}` : ''
    }
  },
  methods: {
    /**
     * 千分比转换
     * @param val 参数
     * @param status 是否转为千分比 true转 false不转
     */
    thousandFun(val = '', status = true) {
      // 是否转为千分比开关 thousand_mark 1开 0关
      const { thousand_mark = 0 } = this.systemConfig
      return isFormatMillageAndNumber(val, status, thousand_mark)
    }
  }
}
</script>

