<template>
  <div class="comp amount_input">
    <div class="amount_input_box input_radius">
      <div class="input_first">{{ symbolText }}</div>
      <div class="input_middle">
        <input
          v-model="amountValue"
          class="input_line"
          type="text"
          :disabled="!isCustom"
          :placeholder="$t('UI_CASHOUT_ENTER_AMOUNT')"
          @input="onInput"
        />
      </div>
      <template v-if="activityInfo.gift_amount">
        <div class="input_last">
          {{ `${$t('UI_Extra')} ${symbolText} ${thousandFun(activityInfo.gift_amount || 0)}` }}
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { isFormatMillageAndNumber } from '@/utils/helper.js'

export default {
  model: {
    prop: 'amount',
    event: 'change'
  },
  props: {
    // 金额
    amount: {
      type: String,
      default: ''
    },
    // 是否为 usdt
    isUsdt: {
      type: Boolean,
      default: false
    },
    // 是否可自定义金额
    isCustom: {
      type: Boolean,
      default: false
    },
    // 活动信息
    activityInfo: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      // 输入金额
      amountValue: ''
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    // 金额标识符显示
    symbolText() {
      // 当前货币符号
      const { currency_symbol = '' } = this.systemConfig
      const symbol = this.currencyShow ? currency_symbol : ''

      return this.isUsdt ? 'USDT' : symbol
    }
  },
  watch: {
    // 监听传入金额
    amount: {
      handler(val = '') {
        this.amountValue = this.thousandFun(val)
      },
      immediate: true
    }
  },
  methods: {
    /**
     * 千分比转换
     * @param val 参数
     * @param status 是否转为千分比 true转为千分比 false转为数字
     * @return {string}
     */
    thousandFun(val = '', status = true) {
      // 是否转为千分比开关 thousand_mark 1开 0关
      const { thousand_mark = 0 } = this.systemConfig
      return isFormatMillageAndNumber(val, status, thousand_mark)
    },
    // 输入框
    onInput(e) {
      const val = e?.target?.value || ''

      let num = val.replace(/[^0-9.,]/g, '')
      // 小数点后保留两位
      if (num.includes('.')) {
        const arr = num.split('.')
        const first = arr[0] || '0'
        let last = arr[1] || ''
        last = last.length > 2 ? last.substring(0, 2) : last

        num = `${first}.${last}`
      }
      // 把千分位金额转为数字金额
      num = this.thousandFun(num, false)
      // 限制12位数
      num = String(num)
      num = num.length > 12 ? num.substring(0, 12) : num

      this.$emit('change', num)
      this.amountValue = this.thousandFun(num)
    }
  }
}
</script>

